<template>
	<div id='home' v-loading.fullscreen="fullscreenLoading" element-loading-text="拼命加载中" element-loading-background="rgba(0, 0, 0, 0.8)">
		<leftNav class='leftNav'></leftNav>
		<div class="center" >
			<topNav class="topNav"></topNav>
			<div class='rightNav'>
				<div class='breadcrumb' :style="{height:breadcrumbHeight+'px'}">
					<tags-view style="width:100%;"/>
				</div>
				<div class='routerView' :style="{height:routerViewHeight+'px'}">
					<transition name="fade-transform" mode="out-in">
						<router-view/>
					</transition>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import topNav from '../../components/nav/topNav.vue'
	import leftNav from '../../components/nav/leftNav.vue'
	import tagsView from '../../components/tagsView/index.vue'
	import { mapState } from 'vuex'

	export default {
		name:'home',
		components:{
			topNav,
			leftNav,
			tagsView,
		},
		data(){
			return{
				centerHeight:0,
				breadcrumbHeight:30,
				routerViewHeight:0,
				fullscreenLoading:false,
			}
		},
		created(){
			this.centerHeight = document.body.clientHeight - 50;
			this.routerViewHeight = this.centerHeight - this.breadcrumbHeight;
			this.resize();
		},

		computed:{
			...mapState({
				needTagsView: state => state.settings.tagsView,
			})
		},

		methods:{
			resize(){
				let self = this;
				window.onresize=function(){
					self.centerHeight = document.body.clientHeight - 50;
					self.routerViewHeight = this.centerHeight - this.breadcrumbHeight;
				}
			}
		},
	}
</script>

<style>
	#home{
		height: 100%;
		display: flex;
		flex-direction: row;
	}

	.topNav{
		position: fixed;
		left: 170px;
		right:20px;
		z-index: 999;
	}

	.center{
		width: 100%;
		display: flex;
		flex-direction: column;
	}

	.leftNav{
		width:150px !important;
		height:100%;
		min-width: 150px;
		z-index: 999;
	}

	.rightNav{
		margin-top: 50px;
		margin-left: 20px;
		margin-right: 20px;
		/* width:100%; */
		height:100%;
	}

	.breadcrumb{
		display: flex;
		align-items: flex-start;
		flex-direction: column;
	}

	.fade-transform-leave-active,
	.fade-transform-enter-active {
		transition: all .5s;
	}

	.fade-transform-enter {
		opacity: 0;
		transform: translateX(-30px);
	}

	.fade-transform-leave-to {
		opacity: 0;
		transform: translateX(30px);
	}
</style>
